@import "../../../styles/const.scss";
.container {
  display: flex;
  flex-wrap: wrap;
  width: 1280px;
  margin: 20px auto;
  &-placeNumber {
    text-align: center;
    color: $text;
    background: #071725;
  }
  &-time {
    border: 1px solid #ffc857;
    margin: 2px;
  }
  &-button {
    position: relative;
    bottom: 0;
    width: 50%;
    cursor: pointer;
    &-add {
      left: 0;
      background-color: $text;
    }
    &-remove {
      right: 0;
      background-color: #ffbace;
    }
  }
}

